import { useEffect, useState } from "react"
import { useParams } from "react-router"
import { DynamicLink } from "../cmps/DynamicLink"
import { cardService } from "../services/cardService"

export function CardDetails(){
    
    const id = useParams()
    const [card, setCard] = useState(null)
    
    useEffect(() => {
        setCard(cardService.getById(id))
    }, [id])
    if (!card) return <h1>Loading</h1>

    const {logo, cover, description, firstName, lastName } = card.data
    return (
        <main className="card-details">
            <img src={cover} className="cover" alt=""/>
            {logo ? <img src={logo} className="logo" alt=""/> : <h1 className="name">{`${firstName} ${lastName}`}</h1>}
            <p className="description">{description}</p>
            <div className="links">
                {card.data.links.map((link) => <DynamicLink link={link}key={Math.random()}/>)}
            </div>
        </main>
    )
}