import WhatsAppIcon from '@material-ui/icons/WhatsApp'
import PhoneIcon from '@material-ui/icons/Phone'
import MailIcon from '@material-ui/icons/Mail';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import { isAndroid, isIOS } from 'react-device-detect';

export function DynamicLink({ link }) {

    function openInstagram() {
        //make openApps with urls based on link type
        if (isAndroid) {
            const url =
                "intent://instagram.com/#Intent;scheme=https;package=com.instagram.android;end";

            window.location.replace(url);
        } else if (isIOS) {
            window.location.replace("instagram://nadav__co");
            setTimeout(() => {
                window.location.replace(
                    "https://apps.apple.com/us/app/instagram/id389801252"
                );
            }, 10000);
        } else {
            window.open("https://instagram.com/nadav__co/");
        }
    }

    switch (link.type) {
        case 'whatsapp':
            return (
                <div className={`${link.type}-link`}><WhatsAppIcon /></div>
            )
        case 'phone':
            return (
                <div className={`${link.type}-link`}><PhoneIcon /></div>
            )
        case 'mail':
            return (
                <div className={`${link.type}-link`}><MailIcon /></div>
            )
        case 'facebook':
            return (
                <div className={`${link.type}-link`}><FacebookIcon /></div>
            )
        case 'instagram':
            return (
                <div onClick={openInstagram} className={`${link.type}-link`}><InstagramIcon /></div>
            )
        case 'twitter':
            return (
                <div className={`${link.type}-link`}><TwitterIcon /></div>
            )
        case 'linkedin':
            return (
                <div className={`${link.type}-link`}><LinkedInIcon /></div>
            )
        default:
            return null

    }
}