import { useState } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import {  useDispatch } from "react-redux"
import { loggin } from "../store/actions/userActions"
import { useHistory } from 'react-router';




export function Login(props) {

  const dispatch = useDispatch()
  const history = useHistory()

  const [user, setUser] = useState({
    username: '',
    password: '',
    isStayLogged: false
  })

  const [msg, setMsg] = useState('')

  const handleChange = (ev) => {
    const { name, value, type, checked } = ev.target
    const val = (type !== 'checkbox') ? value : checked
    setUser({ ...user, [name]: val })
  }

  const doLoggin = async (ev) => {
    ev.preventDefault()
    setMsg('')
    const loggedUser = await dispatch(loggin(user))
    if (loggedUser) history.push('/')
    else setMsg('Wrong username or password!')
  }
  return (
    <Container component="main" maxWidth="xs" >
      <CssBaseline />
      <div style={{
        marginTop: '10px',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
      }}>
        <Avatar style={{
          margin: '10px',
          backgroundColor: 'blue',
        }}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h2" variant="h5">
          Sign in
        </Typography>
        <form
          onSubmit={doLoggin}
          style={{
            width: '100%',
            marginTop: '5px'
          }} noValidate>
          <TextField
            onChange={handleChange}
            variant="filled"
            margin="normal"
            required
            fullWidth
            id="username"
            label="UserName"
            name="username"
            autoFocus

          />
          <TextField
            onChange={handleChange}
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
          />
          <FormControlLabel
            control={<Checkbox 
              name="isStayLogged" 
              onChange={handleChange}
              color="primary" />}
              label="Remember me"
          />
          <p style={{ color: 'red' }}>{msg}</p>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            style={{ marginTop: '10px' }}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item>
              <Link href="#/signup" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}>
      </Box>
    </Container >
  );
}

